.Business {
  position: relative;
  top: 100px;
  display: flex;
  justify-content: center;
}

.container {
  width: 100%;
  font-family: var(--primary-font);
}

@media (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media (min-width: 992px) {
  .Business {
    top: 120px;
  }
  .container {
    width: 992px;
  }
}

@media (min-width: 1200px) {
  .Business {
    top: 140px;
  }
  .container {
    width: 1200px;
  }
}
